


.fw-bolder {
  font-weight: 500 !important;
}
 .table tr {
}
.thead,
tbody,
tfoot,
tr,
td,
th {
  border-style: hidden;
}
.table.table-row-dashed tr {
  border-bottom: 1px solid #cccccc;
}
 .table tbody tr td,
.table thead tr th,
.table thead {
  border: none !important;
}
.table > :not(:first-child) {
  border-top: none !important;
}
.tag .min-w-100px {
  min-width: 31% !important;
}

.table-scroll-task {
  position: relative;
  width: 100%;
  z-index: 1;
  margin: auto;
  overflow: auto;
  height: 700px;
}

.table-scroll {
  position: relative;
  width: 100%;
  z-index: 1;
  margin: auto;
  overflow: auto;
  height: 350px;
}
.table-scroll table {
  width: 100%;

  margin: auto;
  border-collapse: separate;
  border-spacing: 0;
}
.table-wrap {
  position: relative;
}
.table-scroll th,
.table-scroll td {
  vertical-align: top;
}
.table-align .table-scroll th,
.table-align .table-scroll td {
  text-align: center;
}
.table-scroll thead th {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: rgb(255, 255, 255);
}

.table-scroll tfoot,
.table-scroll tfoot th,
.table-scroll tfoot td {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;

  z-index: 4;
  background-color: rgb(255, 255, 255);
}

th:first-child {
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  z-index: 2;
}
thead th:first-child,
tfoot th:first-child {
  z-index: 5;
}
.table-scroll tfoot tr {
  border: none;
}

.datePicker {
  z-index: 6;
  width: 200px;
}

.blink {
  animation: blink 2s steps(5, start) infinite;
  -webkit-animation: blink 1s steps(5, start) infinite;
}
@keyframes blink {
  to {
    visibility: hidden;
  }
}
@-webkit-keyframes blink {
  to {
    visibility: hidden;
  }
}

